:root {
  --accent: #cc8869;
  --text: #333;
  --regular-text: 16px;
  --lineheight: 1.65;
  --userfont: roboto-st, sans-serif;
  --systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif;
  --padding: 120px;
  --transition: cubic-bezier(.4, 0, 0, 1);
}

@font-face {
  src: url("roboto-regular-webfont.cc1e9f53.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 400;
  font-style: normal;
}

@font-face {
  src: url("roboto-italic-webfont.4b4a7be9.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 400;
  font-style: italic;
}

@font-face {
  src: url("roboto-bold-webfont.2456e51b.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 700;
  font-style: normal;
}

@font-face {
  src: url("roboto-bolditalic-webfont.9dbc77d3.woff2") format("woff2");
  font-family: roboto-st;
  font-weight: 700;
  font-style: italic;
}

@font-face {
  src: url("assassin.1648ee8e.woff2") format("woff2");
  font-family: assassin-st;
  font-weight: 700;
  font-style: italic;
}

body {
  font-family: var(--userfont);
  font-size: var(--regular-text);
  line-height: var(--lineheight);
  color: var(--text);
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
}

body, html {
  height: 100%;
}

.showcase {
  height: 100%;
  color: #fff;
  background-color: #000;
  position: relative;
}

.showcase:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  box-shadow: inset 0 0 500px #000;
}

.showcase:after {
  content: "";
  background-image: url("light.08f1b2b1.png");
  background-position: 50% -65px;
  background-repeat: no-repeat;
  background-size: cover;
  animation: 3s ease-in-out infinite k-light;
  position: absolute;
  inset: 0;
}

.showcase__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: .5;
  position: absolute;
  top: 0;
  left: 0;
}

.showcase__content-wrapper {
  z-index: 2;
  height: 100%;
  padding: var(--padding) 0;
  position: relative;
}

.showcase__content-wrapper:before {
  content: "";
  background-image: url("glow.0640825a.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  animation: 1.8s ease-in-out infinite k-glow;
  position: absolute;
  inset: 0;
}

.showcase__header {
  text-align: center;
  width: 100%;
  z-index: 1;
  color: #ffffffbf;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  position: absolute;
  top: 40px;
}

.showcase__header span {
  color: var(--accent);
}

.showcase-carousel .swiper-wrapper {
  transition: var(--transition);
}

.showcase-carousel__item {
  height: calc(100vh - var(--padding) * 2);
  text-align: center;
  opacity: .25;
  transition: opacity 1.8s var(--transition), transform 1.8s var(--transition);
  position: relative;
  transform: scale(.75);
}

.showcase-carousel__item:after {
  content: "";
  width: 120px;
  height: 0;
  position: absolute;
  bottom: 5px;
  left: calc(50% - 60px);
  box-shadow: 0 0 45px 10px #010101;
}

.showcase-carousel__item p {
  width: 100%;
  color: #ffffffbf;
  text-shadow: 0 0 10px #3a4e5e;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  position: absolute;
  bottom: -85px;
}

.showcase-carousel__item.swiper-slide-active {
  opacity: .8;
  transform: scale(.98);
}

.showcase-carousel__image-wrapper > * {
  width: 100%;
  height: 100%;
  perspective: 150px;
  transform-style: preserve-3d;
  position: absolute;
}

.showcase-carousel__image {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
}

.showcase-carousel__image-left {
  perspective-origin: 0;
  -webkit-clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
  clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
}

.showcase-carousel__image-left .showcase-carousel__image {
  animation: 2s ease-in-out infinite alternate k-left-side;
}

.showcase-carousel__image-right {
  perspective-origin: 100%;
  -webkit-clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
  clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
  margin-left: -1.25px;
}

.showcase-carousel__image-right .showcase-carousel__image {
  animation: 2s ease-in-out infinite alternate k-right-side;
}

.showcase-navigation {
  z-index: 2;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.showcase-navigation > * {
  height: 100%;
  width: 50%;
  cursor: pointer;
  outline: none;
  position: absolute;
}

.showcase-navigation__next {
  right: 0;
}

.showcase-navigation > :before {
  content: "?";
  opacity: 0;
  font-family: assassin-st, sans-serif;
  font-size: 45px;
  transition: opacity .75s ease-out;
  position: absolute;
  top: 50%;
  left: 50px;
  transform: rotate(-90deg);
}

.showcase-navigation__next:before {
  left: auto;
  right: 50px;
  transform: rotate(90deg);
}

.showcase-navigation > :hover:before {
  opacity: .5;
}

.showcase-navigation > .swiper-button-disabled {
  display: none;
}

@keyframes k-light {
  0% {
    opacity: .2;
  }

  50% {
    opacity: .3;
  }

  100% {
    opacity: .2;
  }
}

@keyframes k-glow {
  0% {
    opacity: .6;
  }

  50% {
    opacity: .8;
  }

  100% {
    opacity: .6;
  }
}

@keyframes k-left-side {
  0% {
    transform: rotateY(-1deg)scaleX(.92);
  }

  100% {
    transform: rotateY(0)scaleX(1);
  }
}

@keyframes k-right-side {
  0% {
    transform: rotateY(0)scaleX(1);
  }

  100% {
    transform: rotateY(1deg)scaleX(.92);
  }
}

